import {
  uploadFichier,
  login,
  estConnecte,
  seDeconnecter,
  requeteApi,
} from "./content_scripts/poplink";

const VERSION = "2.0";
const urlFrigo = "https://app.integration-its.poplab.education/frigo";
const urlApi = "https://app.integration-its.poplab.education/api";

const browser = require("webextension-polyfill");
/**
     * On utilise le token renvoyé par le login pour se connecter à l'api back.
     * La réponse contient notamment la permission d'utilisation du web clipper.
     * @param {string} token - Token renvoyé lors du login
     * @returns {objet JSON} - JSON de la réponse
     */
async function loginToBackend(token) {
  const loginUrl = `${urlApi}/auth/login`;
  return fetch(loginUrl, {
    method: "POST",
    headers: {
      "Content-Type": "application/json",
      Authorization: `Bearer ${token}`,
    },
  })
    .then((response) => response.json())
    .then((responseData) => responseData)
    .catch((error) => console.warn(error));
}

/**
     * On utilise le token renvoyé par le login pour se déconnecter à l'api back.
     * Cette déconnexion est appelée si l'utilisateur demande une déconnexion via
     * le bouton "se déconnecter" de l'onglet
     * de l'extension ou lors du contrôle du maintien de son authentification..
     * @param {string} token - Token renvoyé lors du login
     */
async function logoutFromBackend(token) {
  const url = `${urlApi}/auth/logout`;
  const req = new XMLHttpRequest();
  req.open("POST", url, true);
  req.setRequestHeader("Accept", "application/json");
  req.setRequestHeader("Authorization", `Bearer ${token}`);

  req.onreadystatechange = function () {
    if (req.readyState === 4) {
      if (req.status === 200) {
        console.log("Success");
      } else if (req.status === 403) {
        console.error("Forbidden");
      }
    }
  };
  req.send();
}

// Envoie un message au content script de l'onglet actif.
/**
     * Envoie un message au content script de l'onglet actif.
     * @param {object} msg
     */
// eslint-disable-next-line consistent-return
async function notifierOngletActif(msg) {
  // Les SVG ne passent pas en content script, à cause de la propriété "all: initial".
  // Ils sont sans couleur et/ou corrompus.
  // Certains sites bloquent les images inline (par exemple, github) par header CSP.
  // La seule option qui marche à 100% est d'ajouter chaque ressource dans le manifest.json
  // (dans web_accessible_resources), d'obtenir leur URI dans le background.js, puis de
  // l'envoyer au content script.
  // https://bugzilla.mozilla.org/show_bug.cgi?id=1267027
  msg.images = {
    add: browser.runtime.getURL("assets/images/add.png"),
    capture: browser.runtime.getURL("assets/images/capture.png"),
    done: browser.runtime.getURL("assets/images/done.png"),
    logo: browser.runtime.getURL("assets/images/logo.svg"),
    selector: browser.runtime.getURL("assets/images/selector.png"),
  };

  // Les API actuelles obligent à gérer les cas où plusieurs onglets sont actifs en même temps
  const activeTabs = await browser.tabs.query({
    active: true,
    currentWindow: true,
  });
  for (const tab of activeTabs) {
    console.log("");
    // Mais en réalité, on n'aura jamais plus d'un onglet actif en même temps.
    return await browser.tabs.sendMessage(tab.id, msg);
  }
}

async function initMenus() {
  const connecte = await estConnecte();
  const errCatch = () => {
    // Une erreur est renvoyée quand l'extension se fait recharger
    // On la vérifie pour pouvoir conserver le rechargement automatique
    if (browser.runtime.lastError) {
      // eslint-disable-next-line no-useless-return
      return;
    }
  };

  browser.contextMenus.create(
    {
      id: "se-deconnecter",
      title: browser.i18n.getMessage("se_deconnecter"),
      contexts: ["browser_action"],
      enabled: connecte,
    },
    errCatch,
  );

  browser.contextMenus.create(
    {
      id: "voir-frigo",
      title: browser.i18n.getMessage("voir_frigo"),
      contexts: ["browser_action"],
    },
    errCatch,
  );

  browser.contextMenus.create(
    {
      id: "envoyer-lien",
      title: browser.i18n.getMessage("envoyer_lien"),
      contexts: ["link"],
      enabled: connecte,
    },
    errCatch,
  );

  browser.contextMenus.create(
    {
      id: "envoyer-image",
      title: browser.i18n.getMessage("envoyer_image"),
      contexts: ["image"],
      enabled: connecte,
    },
    errCatch,
  );

  browser.contextMenus.create(
    {
      id: "envoyer-video",
      title: browser.i18n.getMessage("envoyer_video"),
      contexts: ["video"],
      enabled: connecte,
    },
    errCatch,
  );
  browser.contextMenus.onClicked.addListener(async (info, tab) => {
    if (info.menuItemId === "se-deconnecter") {
      seDeconnecter();
      notifierOngletActif({ type: "stopCapture" });
    } else if (info.menuItemId === "voir-frigo") {
      browser.tabs.create({ url: urlFrigo });
    } else if (info.menuItemId === "envoyer-lien") {
      notifierOngletActif({ type: "capturerLien" });
    } else if (info.menuItemId === "envoyer-image") {
      notifierOngletActif({ type: "capturerImage" });
    } else if (info.menuItemId === "envoyer-video") {
      notifierOngletActif({ type: "capturerVideo" });
    }
  });
}

initMenus();

browser.notifications.onClicked.addListener((idNotif) => {
  browser.tabs.create({ url: urlFrigo });
});
browser.runtime.onMessage.addListener(async (msg, sender) => {
  if (msg.type === "status") {
    /**
     * On contrôle au pop de l'extension que l'utilisateur a la permission d'utiliser le web clipper
     * Si ce n'est pas le cas, les options de scrapping ne s'afficheront pas
     * et le "status" renverra "interdit" afin d'ajouter le message défini dans popup.js
     * @param {string} res - Paramètre pour la récupération de l'objet retourné par la fonction
     */
    const storage1 = localStorage.use_clipper;
    if (storage1 === "interdit") {
      return "interdit";
    }
    let storage = await browser.storage.local.get("access_token");
    if (!storage.access_token) return "déconnecté";
    storage = await browser.storage.local.get(["first_name"]);
    const firstName = storage.first_name || browser.i18n.getMessage("utilisateur_mystere");
    notifierOngletActif({ type: "toggleCapture", firstName });
    return "connecté";
  }
  /**
     * Si l'utilisateur demande le login via le bouton
     * @param {objet} user - Paramètre pour la récupération de l'objet retourné par la fonction
     * @returns {objet JSON} - JSON
     */
  if (msg.type === "connect") {
    const result = await login();
    // const firstName = result.first_name;
    const token = localStorage.access_token;
    const canUseWc = await loginToBackend(token).then((response) => response);
    if (canUseWc.permissions.can_use_webclipper === false) {
      localStorage.use_clipper = "interdit";
      return false;
    }
    // notifierOngletActif({ type: "startCapture", firstName });
    // browser uniquement : ne rien return fait planter les polyfills
    return true;
  }

  if (msg.type === "logout") {
    logoutFromBackend(localStorage.access_token);
    seDeconnecter();
    // notifierOngletActif({ type: "stopCapture" });
    return true;
  }

  if (msg.type === "envoyer_sur_frigo") {
    if (msg.data.titre.length > 100) {
      msg.data.titre = `${msg.data.titre.substring(0, 97)}...`;
    }

    // Envoie l'élément sélectionné sur le frigo, via l'exécuteur en argument.
    // On gère les erreurs de connexion, les erreurs d'envoi et les erreurs génériques.
    // Dans les cas non prévus, on log toutes les données dans la console.
    const uploader = async (executerRequete) => {
      try {
        const res = await executerRequete();

        if (res.errors) {
          throw new Error(res.errors[0].message);
        } else {
          notifierOngletActif({ type: "remercier" });
        }
      } catch (err) {
        browser.notifications.create({
          type: "basic",
          iconUrl: browser.runtime.getURL("assets/images/icon-48.png"),
          title: "Erreur d'importation",
          message: err.message,
        });

        notifierOngletActif({ type: "cancelUpload" });
        if (err.message === "Merci de vous connecter.") {
          seDeconnecter();
        }
      }
    };
    if (msg.type_element === "basique") {
      const storage = await browser.storage.local.get("access_token");
      // Méthode pour contrôler qu'on est toujours authentifié sur keycloak.
      // L'access_token stocké n'est effacé QUE si l'utilisateur clique
      // sur "se déconnecter" via l'onglet de l'extension.
      // Donc si ce token est toujours là, on teste le login via l'appel de la fonction dédiée.
      // Par défaut on se déloggue et reloggue sur l'api back
      if (storage.access_token) {
        // eslint-disable-next-line no-shadow
        const result = await login();
        // const first_name = result.first_name;
        logoutFromBackend(result.access_token);
        await loginToBackend(result.access_token).then((response) => response);
      }
      await uploader(
        requeteApi.bind(
          null,
          { data: msg.data },
        ),
      );
    } else if (msg.type_element === "fichier") {
      const storage = await browser.storage.local.get("access_token");
      // Méthode pour contrôler qu'on est toujours authentifié sur keycloak.
      // L'access_token stocké n'est effacé QUE si l'utilisateur clique
      // sur "se déconnecter" via l'onglet de l'extension.
      // Donc si ce token est toujours là, on teste le login via l'appel de la fonction dédiée.
      // Par défaut on se déloggue et reloggue sur l'api back
      if (storage.access_token) {
        logoutFromBackend(storage.access_token);
        // eslint-disable-next-line no-shadow
        const result = await login();
        await loginToBackend(result.access_token).then((response) => response);
      }
      await uploader(async () => {
        const mime = msg.fichier.split(";")[0].split(":")[1];
        const data = atob(msg.fichier.split(",")[1]);
        const bytes = new Uint8Array(data.length);
        for (const i in data) {
          bytes[i] = data.charCodeAt(i);
        }
        const file = new File(
          [bytes],
          `${msg.data.titre}.${mime.split("/")[1]}`,
          { type: mime },
        );
        return await uploadFichier(msg.type_fichier, file, { data: msg.data });
      });
    }

    return true;
  }

  if (msg.type === "captureTab") {
    // browser ne supporte pas captureTab, uniquement captureVisibleTab
    return await browser.tabs.captureVisibleTab({ format: "png" });
  }

  return "type de message inconnu";
});
